#chevron {
  position: relative;
  text-align: center;
  padding: 12px;
  margin-bottom: 6px;
  height: 350px;
  width: 100%;
}

#chevron:before {
  content: "";
  position: absolute;
  top: -99px;
  left: 0;
  height: 100%;
  width: 32%;
  background: #129a4a;
  transform: skew(0deg, 5deg);
  z-index: 1;
  border-radius: 0 0 90px 0;
}

#chevron:after {
  /* Não mexe */
  content: "";
  /* Não mexe */
  position: absolute;
  /* Posição na vertical */
  top: -136px;
  /* Não mexe, se for mudar a horizontal, mexe no width */
  right: 0;
  height: 100%;
  /* Ajusta cumprimento da barra */
  width: 75%;
  /* Muda cor do bagulho */
  background: linear-gradient(264.87deg, #00ff68 -39.04%, #139548 98.37%);
  /* Aqui rotaciona a barra */
  transform: skew(0deg, -5deg);
  /* Suavidade da borda */
  border-radius: 0 0 0 65px;
  z-index: 0;
}

img {
  z-index: 2;
}

@media screen and (max-width: 1440px) {
  #chevron:before {
    content: "";
    position: absolute;
    top: -110px;
    left: 0;
